import React, { useState } from "react";
import "./App.css";

import Calendar from "./components/Calendar";

const Months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December"
];

const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

function App() {
  const [CalendarVisible, setCalendarVisible] = useState(false);
  const [CheckIn, setCheckIn] = useState("Check-in");
  const [CheckOut, setCheckOut] = useState("Check-out");

  const ShowCalendar = () => {
    setCalendarVisible(!CalendarVisible);
  };

  const setCheckInFunc = date => {
    let month = Months[date.getMonth()].substr(0, 3);
    let d = date.getDate();
    let day = days[date.getDay()];
    setCheckIn(day + ", " + month + " " + d);
  };

  const setCheckOutFunc = date => {
    if (date instanceof Date) {
      let month = Months[date.getMonth()].substr(0, 3);
      let d = date.getDate();
      let day = days[date.getDay()];
      setCheckOut(day + ", " + month + " " + d);
    } else {
      setCheckOut(date);
    }
  };

  return (
    <div className="App">
      <div className="booking-input">
        <div className="datePicker" onClick={ShowCalendar}>
          <div className="icon">
            <img src="./icons/calendar-alt-solid.svg" />
          </div>
          <div className="check-in-date">
            <span>{CheckIn}</span>
          </div>
          <div className="seperator">&ndash;</div>
          <div className="check-out-date">
            <span>{CheckOut}</span>
          </div>
        </div>
        <div className="calendar-holder">
          <Calendar
            visible={CalendarVisible}
            setCheckIn={setCheckInFunc}
            setCheckOut={setCheckOutFunc}
          />
        </div>
      </div>
    </div>
  );
}

export default App;
